////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Intro

.m-startup-intro {
  background-repeat: no-repeat;
  background-position: center top;
  background-size: 100% auto;

  // In JS Modernizr lib adds .webp or .no-webp to the `html` tag, and based on
  // that here we can decide to load the .webp version of image or its .png
  // version.
  .webp & {
    background-image: $m-startup-intro-bg-img-webp;
  }
  .no-webp & {
    background-image: $m-startup-intro-bg-img;
  }
}


.m-startup-intro__logo {
  margin-top: $m-startup-intro-logo-margin-top;

  svg {
    max-width: $m-startup-intro-logo-size;
    max-height: $m-startup-intro-logo-size;
  }

  svg path,
  svg rect,
  svg circle,
  svg polygon {
    fill: $m-color-red;
  }
}

.m-startup-intro__heading {
  margin-top: 0;
  margin-bottom: 16px;
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Proccess

.m-startup-proccess__graphic {
  svg {
    max-width: $m-startup-proccess-graphic-size;
    max-height: $m-startup-proccess-graphic-size;
    fill: currentColor;
  }
}

.m-startup-proccess__graphic--color-red {
  svg path,
  svg rect,
  svg circle,
  svg polygon {
    fill: $m-color-red;
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Offer

.m-startup-offer {
  background-repeat: no-repeat;
  background-position: 95% calc(100% + 20px);
  background-size: 180px;
  background-image: $m-startup-offer-bg-img;

  background-color: $m-color-red;
  color: #fff;
}

.m-startup-offer__btn {
  --f8-button-text-color: #{$m-color-black};
  --f8-button-bg-color: #fff;
  --f8-button-bg-hover-color: #eee;

  margin-top: $m-spacer;
}

.m-startup-offer__badge {
  padding: 6px;
  background-color: rgba(255, 255, 255, 0.2);
  font-size: .8rem;
  vertical-align: middle;
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Adaptation

.m-startup-adaptation {
  background-repeat: no-repeat;
  background-position: 95% calc(100% + 30px);
  background-size: 180px;
  background-image: $m-startup-adaptation-bg-img;
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Prices

.m-startup-prices {
  background-repeat: no-repeat;
  background-position: 95% calc(100% + 30px);
  background-size: 180px;
  background-image: $m-startup-prices-bg-img;

  background-color: $m-color-black;
  color: #fff;

  .m--theme-dark & {
    background-color: #fff;
    color: $m-color-black;
  }
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Cities

.m-startup-cities {
  background-color: $m-color-neutral-bg;
}

.m-startup-cities__city {
  display: inline-block;
  margin: 4px;
  padding: 8px;
  border: solid 2px;
  border-radius: 100px;
  font-size: $m-font-size-sm;
}




////////////////////////////////////////////////////////////////////////////////
////////////////////////////////////////////////////////////////////////////////
/// Foods

.m-startup-footer {
  // We have a large image at the above section of footer... And that image must
  // stick to the footer bg without any space! But the above section line-height
  // prevents that... So that's why we set a negative margin for footer here.
  margin-top: -8px;

  background-color: $m-color-red;
  color: #fff;
}

</style>
